/* footer style */
.footer {
    text-align: center;
    /* py-6 */
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}

.footer a {
    position: relative;
    text-decoration: none;
    cursor: pointer;
}

.footer a:after {
    position: absolute;
    bottom: -3px;
    left: 0;
    content: "";
    width: 0;
    height: 1px;
    transition: all 0.2s linear;
}

/* light and dark mode */
.light_mode_footer {
    color: white;
    background: #1976d2;
}

.light_mode_footer a {
    color: white;
}

.light_mode_footer a:hover::after {
    width: 100%;
    background-color: white;
}

.dark_mode_footer {
    color: white;
    background: black;
}

.dark_mode_footer a {
    color: #1976d2;
}

.dark_mode_footer a:hover::after {
    width: 100%;
    background-color: #1976d2;
}